import { Component, OnInit } from '@angular/core';
import { MessageService, ConfirmationService } from 'primeng/api';
import { DialogService } from 'primeng/dynamicdialog';
import { TableColumn } from '../../mobiles/models/table.columns.model';
import { HardwareAddComponent } from '../hardware-add/hardware-add.component';
import { HardWarePart, HardWarePartGrouped } from '../models/hardware'
import { HardwareService } from "../services/hardware.service"

@Component({
  selector: 'app-hardware-list',
  templateUrl: './hardware-list.component.html',
  styleUrls: ['./hardware-list.component.scss'],
  providers: [MessageService, ConfirmationService, DialogService]
})
export class HardwareListComponent implements OnInit {
  cols: TableColumn[] = [];
  HardwareParts: HardWarePart[] = [];
  selecteHardwarePart: HardWarePart;

  constructor(
    private dialogService: DialogService,
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private HardwareService: HardwareService
  ) { }

  ngOnInit(): void {
    // this.getTableColumns();
    this.getAllHardWareData()
  }

  getTableColumns() {
    let columns: string[] = ["Name", 'Image', "Brand", 'Model', "Price", "DiscountPrice", "Quantity", "highlight", "hotDeal"]
    columns.forEach(column => this.cols.push(new TableColumn(column, column)))
  }

  getAllHardWareData() {
    this.HardwareService.getAllHardwarePart().subscribe((res: HardWarePartGrouped) => {
      this.HardwareParts = res.data;
    })
  }

  openNew(mode: string, data?) {
    const ref = this.dialogService.open(HardwareAddComponent, {
      data: {
        data,
        mode: mode
      },
      header: `${mode} HardwarePart`,
      width: '50%',
      contentStyle: { "max-height": "500px", "overflow": "auto" }
    });

    ref.onClose.subscribe(data => {

      if (data) {
        this.messageService.add({ severity: 'Success', summary: 'HardWare Action', detail: data, life: 3000 });
        this.getAllHardWareData();
      }
    })
  }

  deleteHardwarePart(HardwarePart: HardWarePart) {
    this.confirmationService.confirm({
      message: 'Are you sure you want to delete ' + HardwarePart.hardwarePartTypeName + '?',
      header: 'Confirm',
      icon: 'pi pi-exclamation-triangle',
      accept: () => {
        this.HardwareParts = this.HardwareParts.filter(Acce => Acce.id !== HardwarePart.id);
        this.HardwareService.deleteHardwarePart(HardwarePart.id).subscribe(data => {
          this.messageService.add({ severity: 'success', summary: 'Successful', detail: 'HardwarePart Deleted', life: 3000 });
        })
      }
    });
  }

}
