import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { HardwarePartsRoutingModule } from './hardware-parts-routing.module';
import { HardwareListComponent } from './hardware-list/hardware-list.component';
import { HardwareAddComponent } from './hardware-add/hardware-add.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { ButtonModule } from 'primeng/button';
import { ConfirmDialogModule } from 'primeng/confirmdialog';
import { ContextMenuModule } from 'primeng/contextmenu';
import { DropdownModule } from 'primeng/dropdown';
import { DynamicDialogModule } from 'primeng/dynamicdialog';
import { FileUploadModule } from 'primeng/fileupload';
import { InputNumberModule } from 'primeng/inputnumber';
import { InputTextModule } from 'primeng/inputtext';
import { MultiSelectModule } from 'primeng/multiselect';
import { TableModule } from 'primeng/table';
import { ToastModule } from 'primeng/toast';
import { ToolbarModule } from 'primeng/toolbar';


@NgModule({
  declarations: [HardwareListComponent, HardwareAddComponent],
  imports: [
    CommonModule,
    HardwarePartsRoutingModule,
    FormsModule,
    ReactiveFormsModule,
    TableModule,
    DropdownModule,
    MultiSelectModule,
    ButtonModule,
    ToolbarModule,
    FileUploadModule,
    DynamicDialogModule,
    MultiSelectModule,
    ContextMenuModule,
    ToastModule,
    InputTextModule,
    InputNumberModule,
    ConfirmDialogModule
  ],
  entryComponents: [HardwareAddComponent]
})
export class HardwarePartsModule { }
