import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators, FormArray } from '@angular/forms';
import { MessageService } from 'primeng/api';
import { DynamicDialogRef, DynamicDialogConfig } from 'primeng/dynamicdialog';
import { NgDropDownOption } from '../../mobiles/models/ng-dropdown-option.model';
import { MobileApiService } from '../../mobiles/services/mobile-api.service';
import { HardwareService } from '../services/hardware.service';

@Component({
  selector: 'app-hardware-add',
  templateUrl: './hardware-add.component.html',
  styleUrls: ['./hardware-add.component.scss']
})
export class HardwareAddComponent implements OnInit {
  mode: string;
  data: {};
  deviceBrandOptions: NgDropDownOption[] = [];
  ModelsOptions: NgDropDownOption[] = [];
  HardwarePartTypesOptions: NgDropDownOption[] = [];
  highlightOptions: NgDropDownOption[] = [];
  hotDealOptions: NgDropDownOption[] = [];
  hardwareForm: FormGroup;

  constructor(
    private messageService: MessageService,
    public ref: DynamicDialogRef,
    public config: DynamicDialogConfig,
    private MobileApiService: MobileApiService,
    private HardwareService: HardwareService
  ) { }

  async ngOnInit() {
    this.mode = this.config.data.mode;
    this.createHardwareForm();
    await this.getDeviceBrand();
    await this.getMobileMode();
    await this.getHardwarePartType()
    this.getHighlight();
    this.gethotDeal();
    this.data = this.config.data.data ? this.config.data.data : null;
    if (this.data) {
      console.log(this.data)
      this.hardwareForm.patchValue(this.data);
    }
  }

  createHardwareForm() {
    this.hardwareForm = new FormGroup({
      name: new FormControl('', [Validators.required]),
      description: new FormControl('', [Validators.required]),
      videoUrl: new FormControl('', [Validators.required]),
      hardwarePartTypeId: new FormControl('', [Validators.required]),
      price: new FormControl('', [Validators.required]),
      discountPrice: new FormControl('', [Validators.required]),
      quantity: new FormControl('', [Validators.required]),
      productImage: new FormControl('', [Validators.required]),
      mobileBrandId: new FormControl('', [Validators.required]),
      mobileModelId: new FormControl('', [Validators.required]),
      highlight: new FormControl('', [Validators.required]),
      hotDeal: new FormControl('', [Validators.required]),
    })
  }

  async getDeviceBrand() {
    let deviceBrands: string[] = []
    await this.MobileApiService.getDeviceBrand().toPromise()
      .then(data => {
        deviceBrands = data;
        deviceBrands.forEach((deviceBrand, index) => this.deviceBrandOptions.push({ label: deviceBrand['name'], value: deviceBrand['id'] }))
      })
  }

  async getMobileMode() {
    let mobileModes: string[] = []
    await this.MobileApiService.getMobileModel().toPromise()
      .then(data => {
        mobileModes = data;
        mobileModes.forEach((mobileMode, index) => this.ModelsOptions.push({ label: mobileMode['name'], value: mobileMode['id'] }))
      })
  }

  // getHardwarePartType
  async getHardwarePartType() {
    let HardwarePartTypes: string[] = []
    await this.HardwareService.getHardwarePartType().toPromise()
      .then(data => {
        HardwarePartTypes = data;
        HardwarePartTypes.forEach((HardwarePartType, index) => this.HardwarePartTypesOptions.push({ label: HardwarePartType['name'], value: HardwarePartType['id'] }))
      })
  }

  getHighlight() {
    this.highlightOptions.push({ label: 'True', value: true })
    this.highlightOptions.push({ label: 'False', value: false })
  }

  gethotDeal() {
    this.hotDealOptions.push({ label: 'True', value: true })
    this.hotDealOptions.push({ label: 'False', value: false })
  }

  ImageBaseData: any;

  selectImage(event) {
    let reader = new FileReader();
    reader.readAsDataURL(event.currentFiles[0]);
    reader.onload = () => {
      this.ImageBaseData = reader.result;
      this.ImageBaseData = this.ImageBaseData.split(',')[1];
      this.hardwareForm.patchValue(
        { productImage: this.ImageBaseData }
      )
    };

    reader.onerror = function (error) {
      console.log('Error: ', error);
    };
  }

  handleHardware() {
    if (!this.hardwareForm.valid) {
      this.hardwareForm.markAllAsTouched();
      return this.messageService.add({ severity: 'Error', summary: 'Required Field', detail: 'Required Input', life: 3000 });
    }

    if (this.mode === "Add") {
      this.HardwareService.addHardwarePart(this.hardwareForm.value).subscribe(data => {
        this.ref.close('Hardware Added');
      })
    } else if (this.mode === "Edit") {
      let HardwareEdited = this.hardwareForm.value;
      HardwareEdited['id'] = this.data['id'];
      this.HardwareService.editHardwarePart(this.data['id'], this.hardwareForm.value).subscribe(data => {
        this.ref.close('Hardware Edited');
      })
    }
  }

}
