import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { HardWarePart, HardWarePartGrouped } from '../models/hardware';

@Injectable({
  providedIn: 'root'
})
export class HardwareService {

  private prefix: string = '/HardwarePart'

  constructor(private http: HttpClient) { }

  getHardwarePartType() {
    return this.http.get(`${environment.api}/HardwarePartType`).pipe(map(res => {
      return res['data']
    }))
  }


  getAllHardwarePart() {
    return this.http.get(`${environment.api}/${this.prefix}`).pipe((map((HardwarePart: HardWarePartGrouped) => {
      return HardwarePart;
    })))
  }

  addHardwarePart(HardwarePart: HardWarePart) {
    return this.http.post(`${environment.api}/${this.prefix}`, HardwarePart).pipe(map(res => {
      return res
    }))
  }

  editHardwarePart(HardwarePartId: number, newData: HardWarePart) {
    return this.http.put(`${environment.api}/${this.prefix}/${HardwarePartId}`, newData).pipe(map(res => {
      return res
    }))
  }

  deleteHardwarePart(HardwarePartId: number) {
    return this.http.delete(`${environment.api}/${this.prefix}/${HardwarePartId}`).pipe(map(res => {
      return res
    }))
  }
}
